* {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  font-size: 16px;
  background: #222;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.main {
  position: relative;
  overflow: hidden;
  max-height: 100vh;
}

.header {
  position: absolute;
  width: 100%;
  color: #fff;
  text-align: center;
  font-family: 'Unica One', sans-serif;
  line-height: 3em;
  font-size: 3em;
}

.masonry {
  height: 100vh;
  padding: 10em 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-y: auto;
}

.notfound {
  font-size: 2em;
  line-height: 3em;
  color: #fff;
  text-align: center;
  font-family: 'Unica One', sans-serif;
}

.red {
  font-family: 'Open Sans', sans-serif;
  color: rgb(255, 0, 0);
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}

.green {
  font-family: 'Open Sans', sans-serif;
  color: rgb(11, 188, 11);
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}



